import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import DialogTitle from "@mui/material/DialogTitle";

const dispatchedEvents = [];

const SrsMuiDialogTitle = function (props, ref) {
  return <DialogTitle {...props} theme={props.muiTheme}>{props.content}</DialogTitle>;
};

const styles = `
  /* Custom styles for the MuiDialogTitle component */
`;

const props = [
  {
    name: "content",
    defaultValue: "",
    type: "string",
  },
  {
    name: "disableTypography",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "id",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to DialogTitle if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiDialogTitle),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
